import React from 'react'
import { Col, Row } from 'react-bootstrap'
import Header from './Header'
import { Link } from 'react-router-dom';
import KeyboardBackspaceOutlinedIcon from '@mui/icons-material/KeyboardBackspaceOutlined';

function Blog1() {
  return (
    <div>
      <Row>
        <Col sm={2}>
        <Header/>
        </Col>
        <Col sm={10}>
        <Link to='/Blog'><p className='hrrr'><KeyboardBackspaceOutlinedIcon/></p></Link>

        <Row>
            <Col sm={4} className='watch'>
            <img src="data:image/jpeg;base64," alt="" />
            </Col>
            <Col sm={8}>
            <h2 className='set0'><u><i>Affordable Luxury:</i></u></h2>
            <h6 className='set'><i>One of the most appealing aspects of Timex watches is their affordability without compromising on quality. Timex has managed to strike a balance between high-quality craftsmanship and accessible pricing, democratizing the experience of owning a finely crafted timepiece.
            <br /> 
            <br /> 
            A wristwatch is a device to see how you are spending your day and an expression of style that can add charm to your looks. Watch out for the latest watches for women's designs every fashionable girl should own.
            </i>
        </h6>
         </Col>
      <Row>
        <marquee><strong>TIMEX</strong></marquee>
          <h3><strong><i>1.<u> Luxury Watches</u></i></strong></h3>
              <p>Elegant and sophisticated luxury watches are what you need. They make a strong fashion appeal with their classic yet understated designs. Rose gold and pale pink models have been trending and continue to evoke poise and feminine charm.
                <mark>These women's luxury watches combine the practicality of a timepiece with pure class, making them perfect for special occasions.</mark> Our Timex E Class Women's Rose Gold Dial Round Case Multifunction Function Watch is a great example. It looks opulent and gives a touch of poshness to whatever you wear.
             </p>
             <h3><strong><i>2.<u>Dress Watches</u></i></strong></h3>
               <p> For the professional woman, dress watches are a must-have. <mark>Suitable for official office dressing without looking travagant.</mark> The traditional design often has a white dial against a silver or gold background and a black strap.
                These stylish watches for women exude professionalism and are a great addition to any work ensemble. Our Timex Fashion Women's Rose Gold Dial Round Case Multifunction Function Watch is a perfect example, combining elegance with functionality, ensuring you always look polished and professional.
              </p>
              <h3><strong><i>3.<u>Bracelet Watches</u></i></strong></h3>
               <p> When you cannot decide whether it is a bracelet or a watch, enter the bracelet watches.
               These watches <mark>resemble jewelry in appearance </mark>since the dial hangs from them like bangles or dangling charms to combine both features essential in these adornments. They add glamour to any look, hence appropriate for informal and formal occasions.
             </p>
              <h3><strong><i>4.<u>Smart Watches</u></i></strong></h3>
                <p>Smartwatches are now becoming popular among both men and women. These watches offer a smart and sporty finish that complements various styles. They provide features beyond timekeeping, including touchscreen capabilities and multiple AP locations similar to smartphones.
                Moreover, they also function as fitness monitors due to their integrated accelerometers and heart rate monitors. They notify you when linked to your smartphone, making them a highly versatile accessory. While discussing the best trendy watches for women, dont forget smartwatches that unite technology with style.
              </p>
              <h3><strong><i><u>Conclusion</u></i></strong></h3>
              <p> Watches serve more than telling time; hence, they are fashion accessories that add value and class to what people put on daily. From luxury watches to smartwatches, having different kinds of female watches in your collection means you can be ready for various occasions.
             </p>
         </Row>
        </Row>
        </Col>
      </Row>
    </div>
  )
}

export default Blog1
